<template>
  <div style="display: flex">
    <div id="yb"></div>
    <div>
      <div class="tit">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="名称">
            <el-input v-model="formInline.name" placeholder="输入名称" />
          </el-form-item>
          <el-form-item label="地址">
            <el-input v-model="formInline.address" placeholder="输入地址" />
          </el-form-item>
          <el-form-item label="城市">
            <el-input v-model="formInline.city" placeholder="输入城市" />
          </el-form-item>
          <el-form-item label="手机">
            <el-input v-model="formInline.phone" placeholder="输入手机号" />
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="onSubmit" icon="Search"
              >搜索</el-button
            >
            <el-button type="primary" plain icon="Refresh" @click="clear"
              >重置</el-button
            >
          </el-form-item>
        </el-form>
      </div>
      <el-table
        :data="tableData"
        style="width: 100%; height: calc(100vh - 210px)"
      >
        <el-table-column prop="name" label="名称" width="120" />
        <el-table-column prop="age" label="年龄" width="120" />
        <el-table-column prop="sex" label="性别" width="120" />
        <el-table-column prop="phone" label="手机" width="120" />
        <el-table-column prop="date" label="注册时间" width="150" />
        <el-table-column prop="city" label="城市" width="120" />
        <el-table-column prop="address" label="地址" width="600" />
        <el-table-column prop="pay" label="订单总值/￥" width="120" />
        <el-table-column prop="orderCount" label="订单总数" width="120" />
        <el-table-column prop="vip" label="会员" width="120" />
        <el-table-column fixed="right" label="操作">
          <template #default>
            <el-button link type="primary" size="small" @click="handleClick"
              >详情</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <div
        class="example-pagination-block"
        style="display: flex; justify-content: right"
      >
        <el-pagination
          layout="prev, pager, next"
          :total="50"
          @current-change="changeCurrent"
        />
      </div>
    </div>
  </div>
</template>
<script>
import * as echarts from "echarts";
import { yb } from "../../echartsOptions/yb.js";
import { defineComponent, onMounted, reactive, ref } from "vue";
import { getCustom, getCustomUsers } from "@/mock/mockServe.js";
function clearEchartsBug(ids) {
  ids.forEach((item) => {
    document.getElementById(item).removeAttribute("_echarts_instance_");
  });
}
export default defineComponent({
  setup() {
    const tableData = reactive(getCustomUsers());
    const formInline = reactive({
      name: "",
      city: "",
      phone: "",
      address: "",
    });
    const page = ref(1);
    onMounted(() => {
      // 防止路由切换时 出现空白
      clearEchartsBug(["yb"]);
      const ybChart = echarts.init(document.getElementById("yb"));
      const source = getCustom();
      yb(ybChart, "客户统计", source);
    });
    const handleClick = () => {
      console.log("click");
    };
    const onSubmit = () => {
      tableData.splice(0, tableData.length ? tableData.length : 0);
      tableData.push(...getCustomUsers(formInline));
    };
    const clear = () => {
      (formInline.name = ""),
        (formInline.city = ""),
        (formInline.phone = ""),
        (formInline.address = "");
      tableData.splice(0, tableData.length ? tableData.length : 0);
      tableData.push(...getCustomUsers(formInline));
    };
    const changeCurrent = (value) => {
      console.log(value);
      tableData.splice(0, tableData.length ? tableData.length : 0);
      tableData.push(...getCustomUsers(formInline, value));
    };
    return {
      handleClick,
      tableData,
      formInline,
      onSubmit,
      clear,
      changeCurrent,
      page,
    };
  },
});
</script>
<style lang="scss" scoped>
#yb {
  width: 400px;
  margin-right: 20px;
  height: calc(100vh - 110px);
}
</style>
