<template>
  <div class="Inventory">
    <!-- <div class="charts-tit"></div> -->
    <div class="tit">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="序号">
          <el-input v-model="formInline.id" placeholder="输入序号" />
        </el-form-item>
        <el-form-item label="药品名称">
          <el-input v-model="formInline.name" placeholder="输入名称" />
        </el-form-item>
        <el-form-item label="订单编号">
          <el-input v-model="formInline.serial" placeholder="输入编号" />
        </el-form-item>
        <el-form-item label="客户名称">
          <el-input v-model="formInline.customname" placeholder="输入名称" />
        </el-form-item>
        <el-form-item label="客户电话">
          <el-input v-model="formInline.phone" placeholder="输入电话" />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit" icon="Search"
            >搜索</el-button
          >
          <el-button type="primary" plain icon="Refresh" @click="clear"
            >重置</el-button
          >
        </el-form-item>
      </el-form>
    </div>
    <el-table
      :data="dataList"
      @selection-change="sectionList"
      style="height: calc(100vh - 210px); width: 100%"
    >
      <el-table-column
        type="selection"
        label="选择"
        width="40"
      ></el-table-column>
      <el-table-column prop="id" label="药品序号" width="100">
      </el-table-column>
      <el-table-column prop="serial" label="订单编号" width="300">
      </el-table-column>
      <el-table-column prop="name" label="名称" width="300" />
      <el-table-column prop="count" label="订单数量" width="100" />
      <el-table-column prop="price" label="单价" width="100" />
      <el-table-column prop="guige" label="规格" width="100" />
      <el-table-column prop="guigedetail" label="药品规格" width="200" />
      <el-table-column prop="customname" label="客户名称" width="90" />
      <el-table-column label="订单总价" width="90">
        <template #default="scope"
          ><span @click="test(scope)">{{
            scope.row.count * scope.row.price
          }}</span></template
        >
      </el-table-column>
      <el-table-column prop="address" label="客户地址" width="300" />
      <el-table-column prop="phone" label="客户电话" width="200" />
      <el-table-column prop="date" label="订单日期" width="100" />
      <el-table-column prop="vip" label="会员" width="80" />
      <el-table-column label="操作" width="165" fixed="right">
        <template #default="scope">
          <el-button type="primary" @click="showList(scope.$index)"
            >编辑</el-button
          >
          <el-button type="danger" @click="delet(scope.$index, scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <div
      class="example-pagination-block"
      style="display: flex; justify-content: right; height: 50px"
    >
      <el-pagination
        layout="prev, pager, next"
        :total="1000"
        v-model="current"
        @current-change="changeCurrent"
      />
    </div>
    <el-dialog width="30%" v-model="dialogTableVisible" title="订单修改">
      <el-form :model="form" label-width="120px">
        <el-form-item label="订单编号">
          <el-input v-model="form.name" />
        </el-form-item>
        <el-form-item label="客户名称">
          <el-input v-model="form.manufacturer" />
        </el-form-item>
        <el-form-item label="发货时间">
          <el-col>
            <el-date-picker
              format="YYYY-MM-DD"
              v-model="form.date1"
              type="date"
              placeholder="发货时间"
              style="width: 100%"
            />
          </el-col>
        </el-form-item>
        <el-form-item label="图片上传/拖拽">
          <el-upload
            action="#"
            list-type="picture-card"
            :auto-upload="trye"
            :drag="true"
            ref="upload"
          >
            <el-icon><Plus /></el-icon>

            <template #file="{ file }">
              <div>
                <img
                  class="el-upload-list__item-thumbnail"
                  :src="file.url"
                  alt=""
                />
                <span class="el-upload-list__item-actions">
                  <span
                    class="el-upload-list__item-preview"
                    @click="handlePictureCardPreview(file)"
                  >
                    <el-icon><zoom-in /></el-icon>
                  </span>
                  <span
                    v-if="!disabled"
                    class="el-upload-list__item-delete"
                    @click="handleDownload(file)"
                  >
                    <el-icon><Download /></el-icon>
                  </span>
                  <span
                    v-if="!disabled"
                    class="el-upload-list__item-delete"
                    @click="handleRemove(file)"
                  >
                    <el-icon><Delete /></el-icon>
                  </span>
                </span>
              </div>
            </template>
          </el-upload>

          <el-dialog v-model="dialogVisible">
            <img
              w-full
              :src="dialogImageUrl"
              style="width: 100%"
              alt="Preview Image"
            />
          </el-dialog>
        </el-form-item>
        <el-form-item label="类型">
          <el-checkbox-group v-model="form.type">
            <el-checkbox label="网购" name="type" />
            <el-checkbox label="线下批发" name="type" />
            <el-checkbox label="电话订单" name="type" />
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="订单描述">
          <el-input v-model="form.desc" type="textarea" height="300px" />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmitfn">修改</el-button>
          <el-button @click="clean">重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import { defineComponent, reactive, ref } from "vue";
import { getOrder } from "@/mock/mockServe.js";
import { ElMessage, ElMessageBox } from "element-plus";
export default defineComponent({
  setup() {
    const dialogTableVisible = ref(false);
    const dialogFormVisible = ref(false);
    const seList = ref(null);
    let url = ref("../../assets/buluofen.jpg");
    // 表单数据
    let dataList = ref(getOrder());
    const formInline = reactive({
      name: "",
      serial: "",
      id: "",
      customname: "",
      phone: "",
    });
    const current = ref(1);
    const pageSize = ref(20);
    const changeCurrent = (index) => {
      console.log(index);
      dataList.value = getOrder({
        ...formInline,
        page: index,
        pageSize: pageSize.value,
      });
    };
    const sectionList = (List) => {
      seList.value = List;
    };
    const showList = (index) => {
      console.log(index);
      dialogTableVisible.value = true;
    };
    const handleClick = () => {
      console.log(1);
    };
    const clear = () => {
      formInline.name = "";
      formInline.customname = "";
      formInline.phone = "";
      formInline.serial = "";
      formInline.id = "";
      dataList.value = getOrder({
        ...formInline,
      });
    };
    const test = (value) => {
      console.log(value);
    };
    const delet = (index, row) => {
      ElMessageBox.confirm("是否要删除该数据?", "warning ", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          const itemIndex = dataList.value.indexOf(row);
          dataList.value.splice(itemIndex, 1);
          // 勾选删除
          if (seList.value.length) {
            seList.value.forEach((item) => {
              const index = dataList.value.indexOf(item);
              dataList.value.splice(index, 1);
            });
          }
          ElMessage({
            type: "success",
            message: "已删除",
          });
        })
        .catch(() => {
          ElMessage({
            type: "info",
            message: "已取消",
          });
        });
    };
    // 搜索
    const onSubmit = () => {
      console.log(formInline);
      dataList.value = getOrder({
        ...formInline,
      });
    };
    // do not use same name with ref
    const form = reactive({
      name: "",
      region: "",
      date1: "",
      date2: "",
      delivery: false,
      type: [],
      price: "",
      desc: "",
    });
    const upload = ref();
    const onSubmitfn = () => {
      ElMessage({
        message: "",
        type: "success",
      });
    };
    const dialogImageUrl = ref("");
    const dialogVisible = ref(false);
    const disabled = ref(false);
    const clean = () => {
      Object.keys(form).forEach((key) => {
        if (key == "delivery") {
          form[key] = false;
          return;
        }
        if (key == "type") {
          form[key] = [];
          return;
        }
        form[key] = "";
      });
      dialogImageUrl.value = "";
      dialogVisible.value = false;
      upload.value.clearFiles();
    };
    const handleRemove = (file) => {
      console.log(file);
    };

    const handlePictureCardPreview = (file) => {
      dialogImageUrl.value = file.url;
      dialogVisible.value = true;
    };

    const handleDownload = (file) => {
      console.log(file);
    };
    return {
      dialogTableVisible,
      dialogFormVisible,
      formInline,
      handleClick,
      changeCurrent,
      showList,
      delet,
      onSubmit,
      sectionList,
      clear,
      dataList,
      url,
      current,
      pageSize,
      dialogImageUrl,
      dialogVisible,
      disabled,
      handleRemove,
      upload,
      handlePictureCardPreview,
      form,
      handleDownload,
      onSubmitfn,
      clean,
      test,
    };
  },
});
</script>
