export function xt(myChart) {
  var option;

  option = {
    legend: {},
    tooltip: {},
    dataset: {
      source: [
        ["product", "西药", "中药", "化学药"],
        ["2023", 43.3, 85.8, 93.7],
        ["2022", 83.1, 73.4, 55.1],
        ["2021", 86.4, 65.2, 82.5],
      ],
    },
    xAxis: { type: "category" },
    yAxis: {},
    series: [{ type: "bar" }, { type: "bar" }, { type: "bar" }],
  };
  option && myChart.setOption(option);
}
