      ></el-table-column>
      <el-table-column prop="id" label="药品序号" width="100px">
      </el-table-column>
      <el-table-column prop="name" label="名称" width="500px" />
      <el-table-column prop="count" label="库存数量" width="100px" />
      <el-table-column prop="price" label="价格/元" width="100px" />
      <el-table-column prop="manufacturer" label="厂商" width="500px" />
      <el-table-column prop="date" label="日期" width="200px" />
      <el-table-column prop="address" label="地址" width="400px" />
      <el-table-column label="操作" width="300px" fixed="right">
        <template #default="scope">
          <el-button type="primary" @click="showList(scope.$index)"
            >编辑</el-button
          >
          <el-button type="danger" @click="delet(scope.$index, scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <div
      class="example-pagination-block"
