          <el-input v-model="form.name" />
        </el-form-item>
        <el-form-item label="厂商">
          <el-input v-model="form.manufacturer" />
        </el-form-item>
        <el-form-item label="入库时间">
          <el-col :span="11">
            <el-date-picker
              v-model="form.date1"
              type="date"
              placeholder="入库时间"
              style="width: 100%"
            />
          </el-col>
          <el-col :span="1" class="text-center">
            <span
              class="text-gray-500"
              style="width: 100%; display: inline-block; text-align: center"
              >-</span
            >
          </el-col>
          <el-col :span="11">
            <el-time-picker
              v-model="form.date2"
              placeholder="入库时间"
              style="width: 100%"
            />
          </el-col>
        </el-form-item>
        <el-form-item label="图片上传/拖拽">
          <el-upload
            action="#"
            list-type="picture-card"
            :auto-upload="trye"
            :drag="true"
            ref="upload"
          >
            <el-icon><Plus /></el-icon>

            <template #file="{ file }">
              <div>
                <img
                  class="el-upload-list__item-thumbnail"
                  :src="file.url"
                  alt=""
                />
                <span class="el-upload-list__item-actions">
                  <span
                    class="el-upload-list__item-preview"
                    @click="handlePictureCardPreview(file)"
                  >
                    <el-icon><zoom-in /></el-icon>
                  </span>
                  <span
                    v-if="!disabled"
                    class="el-upload-list__item-delete"
                    @click="handleDownload(file)"
                  >
                    <el-icon><Download /></el-icon>
                  </span>
                  <span
                    v-if="!disabled"
                    class="el-upload-list__item-delete"
                    @click="handleRemove(file)"
                  >
                    <el-icon><Delete /></el-icon>
                  </span>
                </span>
              </div>
            </template>
          </el-upload>

          <el-dialog v-model="dialogVisible">
            <img
              w-full
              :src="dialogImageUrl"
              style="width: 100%"
              alt="Preview Image"
            />
          </el-dialog>
        </el-form-item>
        <el-form-item label="类型">
          <el-checkbox-group v-model="form.type">
            <el-checkbox label="中药" name="type" />
            <el-checkbox label="西药" name="type" />
            <el-checkbox label="处方药" name="type" />
            <el-checkbox label="中成药" name="type" />
            <el-checkbox label="化学药" name="type" />
            <el-checkbox label="生物药" name="type" />
            <el-checkbox label="药饮片" name="type" />
            <el-checkbox label="中药材" name="type" />
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="类型">
          <el-checkbox-group v-model="form.type">
            <el-checkbox label="单菌霉素类" name="type" />
            <el-checkbox label="抗菌药物" name="type" />
            <el-checkbox label="抗高血压药物" name="type" />
            <el-checkbox label="抗心律失常药物" name="type" />
            <el-checkbox label="抗胃酸分泌" name="type" />
            <el-checkbox label="疏肝理气" name="type" />
            <el-checkbox label="青霉素类" name="type" />
            <el-checkbox label="氨基苷类" name="type" />
            <el-checkbox label="林可酰胺" name="type" />
            <el-checkbox label="喹诺酮类" name="type" />
            <el-checkbox label="大环内酯类" name="type" />
            <el-checkbox label="四环素类" name="type" />
            <el-checkbox label="单环β-内酰胺" name="type" />
            <el-checkbox label="氨基糖苷类" name="type" />
            <el-checkbox label="酰胺醇类" name="type" />
            <el-checkbox label="大环内酯类" name="type" />
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="药品单价">
          <el-input v-model="form.price" />
        </el-form-item>
        <el-form-item label="药品描述">
          <el-input v-model="form.desc" type="textarea" height="300px" />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmitfn">修改</el-button>
          <el-button @click="clean">重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import { defineComponent, reactive, ref } from "vue";
import { getInventory } from "@/mock/mockServe.js";
import { ElMessage, ElMessageBox } from "element-plus";
export default defineComponent({
  setup() {
    const dialogTableVisible = ref(false);
    const dialogFormVisible = ref(false);
    const seList = ref(null);
    let url = ref("../../assets/buluofen.jpg");
    // 表单数据
    let dataList = ref(getInventory());
    const formInline = reactive({
      name: "",
      manufacturer: "",
      id: "",
    });
    const current = ref(1);
    const pageSize = ref(20);
    const changeCurrent = (index) => {
      console.log(index);
      dataList.value = getInventory({
        id: formInline.id,
        name: formInline.name,
        manufacturer: formInline.manufacturer,
        page: index,
        pageSize: pageSize.value,
      });
    };
    const sectionList = (List) => {
      seList.value = List;
    };
    const showList = (index) => {
      console.log(index);
      dialogTableVisible.value = true;
    };
    const handleClick = () => {
      console.log(1);
    };
    const clear = () => {
      formInline.name = "";
      formInline.manufacturer = "";
      formInline.id = "";
      dataList.value = getInventory({
        id: formInline.id,
        name: formInline.name,
        manufacturer: formInline.manufacturer,
      });
    };
    const delet = (index, row) => {
      ElMessageBox.confirm("是否要删除该数据?", "warning ", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          const itemIndex = dataList.value.indexOf(row);
          dataList.value.splice(itemIndex, 1);
          // 勾选删除
          if (seList.value.length) {
            seList.value.forEach((item) => {
              const index = dataList.value.indexOf(item);
              dataList.value.splice(index, 1);
            });
          }
          ElMessage({
            type: "success",
            message: "已删除",
          });
        })
        .catch();
    };
    // 搜索
    const onSubmit = () => {
      dataList.value = getInventory({
        id: formInline.id,
        name: formInline.name,
        manufacturer: formInline.manufacturer,
      });
    };
    // do not use same name with ref
    const form = reactive({
      name: "",
      region: "",
      date1: "",
      date2: "",
      delivery: false,
      type: [],
      price: "",
      desc: "",
    });
    const upload = ref();
    const onSubmitfn = () => {
      ElMessage({
        message: "",
        type: "success",
      });
    };
    const dialogImageUrl = ref("");
    const dialogVisible = ref(false);
    const disabled = ref(false);
    const clean = () => {
      Object.keys(form).forEach((key) => {
        if (key == "delivery") {
          form[key] = false;
          return;
        }
        if (key == "type") {
          form[key] = [];
          return;
        }
        form[key] = "";
      });
      dialogImageUrl.value = "";
      dialogVisible.value = false;
      upload.value.clearFiles();
    };
    const handleRemove = (file) => {
      console.log(file);
    };

    const handlePictureCardPreview = (file) => {
      dialogImageUrl.value = file.url;
      dialogVisible.value = true;
    };

    const handleDownload = (file) => {
      console.log(file);
    };
    return {
      dialogTableVisible,
      dialogFormVisible,
      formInline,
      handleClick,
      changeCurrent,
      showList,
      delet,
      onSubmit,
      sectionList,
      clear,
      dataList,
      url,
      current,
      pageSize,
      dialogImageUrl,
      dialogVisible,
      disabled,
      handleRemove,
      upload,
      handlePictureCardPreview,
      form,
      handleDownload,
      onSubmitfn,
      clean,
    };
  },
});
</script>
