import { hookey } from "./hookKey.js";

export function filterHook(result, params, ApiKeys) {
  params = {
    ...params,
  };
  if (!params.page) {
    params.page = 1;
    params.pageSize = 20;
  }
  const res = result.filter((item) => {
    if (hookey(params, item, ApiKeys)) {
      return item;
    }
  });
  if (params.page && params.pageSize) {
    const start = (params.page - 1) * params.pageSize;
    const end = params.pageSize * params.page;
    return res.slice(start, end);
  }
  return res;
}
