<template>
  <div class="top">
    <el-icon size="30" class="flod" @click="showMenusHandle"
      ><Fold v-if="collapse == false"></Fold><Expand v-else></Expand
    ></el-icon>
  </div>
</template>
<script>
import { defineComponent, ref } from "vue";
export default defineComponent({
  setup(prop, { emit }) {
    let collapse = ref(false);
    const showMenusHandle = () => {
      collapse.value = !collapse.value;
      emit("menusCollapse", collapse.value);
    };
    return { showMenusHandle, collapse };
  },
});
</script>
<style lang="scss">
.top {
  display: flex;
  align-items: center;
  .flod {
    cursor: pointer;
  }
}
</style>
