/* eslint-disable no-constant-condition */
import Mock from "mockjs";

import Menus from "./data/Menus.json";
import Inventory from "./data/Inventory.json";
import Echarts from "./data/Echarts.json";
import Order from "./data/orderControl.json";
import custom from "./data/custom.json";
import customUser from "./data/customUser.json";
// hook
import { filterHook } from "./hook/resultHook.js";
const code = 200;

export function getMenus() {
  const result = Mock.mock("/Menus", { code, data: Menus })._mocked["/Menus"]
    .template.data.menuList;
  return result;
}

// 库存
export function getInventory(params) {
  // 接口可以传递的参数
  const ApiKeys = {
    //equivalence相等查询 //indistinct模糊查询
    id: "equivalence",
    date: "equivalence",
    name: "indistinct",
    manufacturer: "indistinct",
  };
  let result = Mock.mock("/Inventory", { code, data: Inventory })._mocked[
    "/Inventory"
  ].template.data.list;
  // 参数过滤
  return (result = filterHook(result, params, ApiKeys));
}

