export function getCustomUsers(params, page) {
  const ApiKeys = {
    //equivalence相等查询 //indistinct模糊查询
    // date: "equivalence",
    name: "indistinct",
    address: "equivalence",
    phone: "equivalence",
    city: "equivalence",
  };
  let result = Mock.mock("/CustomUser", { code, data: customUser })._mocked[
    "/CustomUser"
  ].template.data.list;
  const par = { ...params, page: page, pageSize: 30 };
  if (!par.page) {
    par.page = 1;
  }
  return (result = filterHook(result, par, ApiKeys));
}
