export function yb(myChart, tit, sourceList) {
  if (!tit) {
    tit = "药品分类统计";
  }
  let source = [
    ["product", "1月", "2月", "3月", "4月", "5月", "6月"],
    ["生物药", 56.5, 82.1, 88.7, 70.1, 53.4, 85.1],
    ["化学药", 51.1, 51.4, 55.1, 53.3, 73.8, 68.7],
    ["中药", 40.1, 62.2, 69.5, 36.4, 45.2, 32.5],
  ];
  if (sourceList) {
    source = sourceList;
  }
  var option;
  option = {
    title: {
      text: tit,
    },
    legend: {
      orient: "vertical",
      right: 30,
    },
    tooltip: {
      trigger: "axis",
      showContent: false,
    },
    dataset: {
      source,
    },
    xAxis: { type: "category" },
    yAxis: { gridIndex: 0 },
    grid: { top: "55%" },
    series: [
      {
        type: "line",
        smooth: true,
        seriesLayoutBy: "row",
        emphasis: { focus: "series" },
      },
      {
        type: "line",
        smooth: true,
        seriesLayoutBy: "row",
        emphasis: { focus: "series" },
      },
      {
        type: "line",
        smooth: true,
        seriesLayoutBy: "row",
        emphasis: { focus: "series" },
      },
      {
        type: "pie",
        id: "pie",
        radius: "30%",
        center: ["50%", "25%"],
        emphasis: {
          focus: "self",
        },
        label: {
          formatter: "{b}: {@1} ({d}%)",
        },
        encode: {
          itemName: "product",
          value: 1,
          tooltip: 1,
        },
      },
    ],
  };
  myChart.on("updateAxisPointer", function (event) {
    const xAxisInfo = event.axesInfo[0];
    if (xAxisInfo) {
      const dimension = xAxisInfo.value + 1;
      myChart.setOption({
        series: {
          id: "pie",
          label: {
            formatter: "{b}: {@[" + dimension + "]} ({d}%)",
          },
          encode: {
            value: dimension,
            tooltip: dimension,
          },
        },
      });
    }
  });
  myChart.setOption(option);

  option && myChart.setOption(option);
}
